<template>
    <div id="app" class="relative">
        <loading v-if="this.$store.state.isloadingShow"></loading>
        <router-view></router-view>
        <div class="cookie shadow-lg" v-if="cookieShow && !this.$store.state.isloadingShow ">
            <div class="flex justify-between">
                <div class="md:grid grid-cols-6 gap-6 relative">
                    <div class="col-span-5 p-5 ">
                        <div class="title font-mono">This site uses cookies</div>
                        <div class="content">We use cookies to optimize your user experience as well as to send improved
                            content. If you continue to browse our website, you accept these cookies. You can
                            personalize and change your consent choices by returning at any time to this site.
                        </div>
                    </div>
                    <div>
                        <div class="rightBtn">
                            <button class="inline-flex items-center justify-center font-mono uppercase transition ease-in duration-200 hover:no-underline active:shadow-inner focus:outline-none rounded-sm h-10 px-4 text-sm border text-white shadow hover:shadow-hover hover:border-transparent active:border-transparent bg-black border-black hover:text-yellow-100"
                                    @click="closeCookie"
                            >Ok,accept all
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Loading from '@components/loading';

export default {
  name: 'app',
  data() {
    return {
      cookieShow: false,
    };
  },
  components: {
    Loading,
  },
  methods: {
    closeCookie() {
      this.cookieShow = false;
      localStorage.setItem('cookieShow', false);
    },
  },
  mounted() {
    const cookie = localStorage.getItem('cookieShow');
    this.cookieShow = cookie === null ? true : JSON.parse(cookie);
  },
};
</script>

