    .cookie {
        position: fixed;
        left: 0;
        bottom: 0;
        background: #fafafa;
        z-index: 9999;

        .title {
            font-weight: bold;
        }

        .content {
            font-size: 14px;
        }

        .remove {
            position: absolute;
            right: 10px;
            top: 50%;
            cursor: pointer;
        }

        .rightBtn {
            margin-top: 15%;
            margin-right: 10px;
        }
    }

    @media (max-width: 768px) {
        .rightBtn {
            margin-top: 0 !important;
        }

        .remove {
            top: 10% !important;
        }
    }
</style>
